<template>
  <div class="container mt-4">
    <div class="tile justify-content-between align-items-center mb-3">
      <h1>The Article üì∞</h1>
      <router-link to="/criar" class="btn btn-success">+ Criar Artigo</router-link>
    </div>

    <!-- Filtros -->
    <div class="mb-3 tile" style="gap: .5rem;">
      <input v-model="busca" class="form-control" placeholder="Buscar por t√≠tulo..." />
      <select v-model="categoriaSelecionada" class="form-control" style="max-width: 220px;">
        <option value="">Todas as categorias</option>
        <option v-for="cat in categoriasUnicas" :key="cat" :value="cat">{{ cat }}</option>
      </select>
      <select v-model="ordenacao" class="form-control" style="max-width: 180px;">
        <option value="recentes">Mais recentes</option>
        <option value="antigos">Mais antigos</option>
        <option value="titulo">Por t√≠tulo (A‚ÄìZ)</option>
      </select>
    </div>

    <div v-if="loading" class="text-center">Carregando...</div>

    <div v-else>
      <article v-for="artigo in artigosFiltrados" :key="artigo.id" class="artigo" @click="abrirArtigo(artigo.id)">
        <h2 class="mb-1">{{ artigo.titulo }}</h2>
        <small class="simple-text d-block mb-2">
          üè∑Ô∏è {{ artigo.categoria || 'geral' }} ‚Ä¢ üìÖ {{ formatDate(artigo.dataPublicacao) }}
        </small>
        <p>{{ primeiroParagrafo(artigo) }}</p>
        <small class="simple-text">üëÅÔ∏è {{ artigo.views || 0 }} ‚Ä¢ ‚ù§Ô∏è {{ artigo.likes || 0 }}</small>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      artigos: [],
      loading: true,
      busca: '',
      categoriaSelecionada: '',
      ordenacao: 'recentes'
    }
  },
  async created() {
    try {
      const r = await fetch('http://localhost:5000/api/artigos')
      this.artigos = await r.json()
    } catch (e) {
      console.error('Erro ao buscar artigos:', e)
    } finally {
      this.loading = false
    }
  },
  computed: {
    categoriasUnicas() {
      const set = new Set(this.artigos.map(a => a.categoria || 'geral'))
      return Array.from(set).sort((a, b) => a.localeCompare(b))
    },
    artigosFiltrados() {
  // c√≥pia defensiva
  let lista = [...this.artigos];

  const normalizar = (v) => (v || '').toString().toLowerCase();

  // --- FILTRO POR CATEGORIA ---
  if (this.categoriaSelecionada) {
    const cat = normalizar(this.categoriaSelecionada);
    lista = lista.filter(a => normalizar(a.categoria) === cat);
  }

  // --- FILTRO POR T√çTULO ---
  if (this.busca) {
    const termo = normalizar(this.busca);
    lista = lista.filter(a => normalizar(a.titulo).includes(termo));
  }

  // --- ORDENA√á√ÉO ---
  const getData = (d) => {
    // converte "9/11/2025" corretamente para dd/mm/yyyy
    if (!d) return 0;
    const parts = d.split('/');
    if (parts.length === 3) {
      const [dia, mes, ano] = parts;
      return new Date(`${ano}-${mes}-${dia}T00:00:00`).getTime();
    }
    // fallback caso j√° seja ISO
    return new Date(d).getTime() || 0;
  };

  switch (this.ordenacao) {
    case 'titulo':
      lista.sort((a, b) =>
        normalizar(a.titulo).localeCompare(normalizar(b.titulo))
      );
      break;

    case 'antigos':
      lista.sort((a, b) => getData(a.dataPublicacao) - getData(b.dataPublicacao));
      break;

    default: // recentes
      lista.sort((a, b) => getData(b.dataPublicacao) - getData(a.dataPublicacao));
      break;
  }

  return lista;
}

  },
  methods: {
    abrirArtigo(id) {
      const destino = `/article/${id}`
      if (this.$route.path !== destino) this.$router.push(destino)
    },
    primeiroParagrafo(artigo) {
      const p = (artigo.conteudo && artigo.conteudo[0]) || ''
      return p.length > 140 ? p.substring(0, 140) + '...' : p
    },
    formatDate(iso) {
      if (!iso) return 'sem data';

      // divide 9/11/2025 em [9,11,2025] (DD/MM/YYYY)
      const [dia, mes, ano] = iso.split('/');

      const d = new Date(`${ano}-${mes}-${dia}T00:00:00`);
      if (isNaN(d)) return iso;

      return d.toLocaleDateString('pt-BR');
    },
  }
}
</script>

<style scoped>
.artigo {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 1.2rem;
  margin: 1rem 0;
  color: var(--text-color);
  cursor: pointer;
  transition: background-color .3s ease, transform .2s ease;
}

.artigo:hover {
  transform: scale(1.01);
}
</style>
