<template>
  <div class="container mt-5">
    <router-link to="/" class="btn btn-secondary mb-3">‚Üê Voltar</router-link>

    <div v-if="loading">Carregando artigo...</div>

    <div v-else-if="artigo">
      <h1>{{ artigo.titulo }}</h1>
      <p class="simple-text">
        üè∑Ô∏è {{ artigo.categoria }} ‚Ä¢
        üìÖ {{ formatDate(artigo.dataPublicacao) }} ‚Ä¢
        üëÅÔ∏è {{ artigo.views || 0 }} ‚Ä¢
        ‚ù§Ô∏è {{ artigo.likes || 0 }}
      </p>
      <div class="mb-3">
        <button class="btn btn-warning mr-2  me-2" @click="editarArtigo">‚úèÔ∏è Editar</button>
        <button class="btn btn-danger mr-2  me-2" @click="deletarArtigo">üóëÔ∏è Deletar</button>
        <button class="btn btn-success mr-2  me-2" @click="curtirArtigo(artigo.id)">üëç Curtir</button>
        <button class="btn btn-info" @click="exportarPDF">üìÑ Exportar PDF</button>
      </div>

      <div v-for="(p, i) in artigo.conteudo" :key="i" class="paragrafo">
        <p>{{ p }}</p>
      </div>

      <!-- Coment√°rios -->
      <div class="mt-5">
        <h3>üí¨ Coment√°rios</h3>
        <div v-if="artigo.comentarios && artigo.comentarios.length">
          <div v-for="(c, i) in artigo.comentarios" :key="i" class="comentario border-bottom pb-2 mb-2">
            <p><strong>{{ c.autor }}</strong> ‚Äî <small>{{ c.data }}</small></p>
            <p>{{ c.texto }}</p>
            <button class="btn btn-danger mr-2  me-2" @click="deletarComet√°rio(c.id)">üóëÔ∏è Deletar</button>
          </div>
        </div>
        <div v-else class="simple-text">Ainda n√£o h√° coment√°rios.</div>

        <div class="mt-3">
          <input v-model="novoComentario" placeholder="Escreva um coment√°rio..." class="form-control mb-2" />
          <button class="btn btn-primary" @click="enviarComentario">Enviar</button>
        </div>
      </div>
    </div>

    <div v-else>
      <p>Artigo n√£o encontrado.</p>
    </div>
  </div>
</template>

<script>
import jsPDF from 'jspdf'

export default {
  props: ['id'],
  data() {
    return {
      artigo: null,
      loading: true,
      novoComentario: '',
      senha: 'Aprovado'
    }
  },
  async created() {
    try {
      const resposta = await fetch(`/api/artigos/${this.id}`)
      this.artigo = await resposta.json()
    } catch (error) {
      console.error('Erro ao carregar artigo:', error)
    } finally {
      this.loading = false
    }
  },
  methods: {
    seguranca() {
      const senha = prompt("Digite a senha para prosseguir:");

      if (senha !== this.senha) {
        alert("Senha incorreta.");
        return false;
      }
      return true;
    },
    editarArtigo() {
      if (!this.seguranca()) return;
      this.$router.push(`/editar/${this.id}`)
    },
    async deletarArtigo() {
      if (!this.seguranca()) return;
      if (!confirm('Tem certeza que deseja deletar este artigo? üò¢')) return
      try {
        const resposta = await fetch(`/api/artigos/${this.id}`, {
          method: 'DELETE'
        })
        const data = await resposta.json()
        alert(data.mensagem || 'Artigo deletado com sucesso!')
        this.$router.push('/')
      } catch (error) {
        console.error('Erro ao deletar artigo:', error)
      }
    },

    async deletarComet√°rio(idComentario) {
      if (!confirm('Tem certeza que deseja deletar este coment√°rio? üò¢')) return
      try {
        const resposta = await fetch(`/api/artigos/${this.id}/comentarios`, {
          method: 'DELETE',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            idComentario: idComentario
          })
        })
        const data = await resposta.json()
        alert(data.mensagem || 'Coment√°rio deletado com sucesso!')
        this.artigo.comentarios = data.comentarios;
      } catch (error) {
        console.error('Erro ao deletar artigo:', error)
      }
    },
    async curtirArtigo(id) {
      let curtidos = JSON.parse(localStorage.getItem('artigosCurtidos')) || [];

      let curtidas = curtidos.find(item => item.id == id);
      console.log('iii', curtidas);

      let rota = curtidas && curtidas.curtir ? 'remover' : 'curtir';

      let resposta = await fetch(`/api/artigos/${id}/${rota}`, { method: 'POST' });

      if (!resposta.ok) return alert("Erro ao atualizar curtida");
      const dados = await resposta.json();
      console.log(dados);

      if (curtidas) {
        curtidas.curtir = dados.curtido
      } else {
        curtidos.push({ id: id, curtir: true });
      }

      this.artigo.likes = dados.likes;

      localStorage.setItem('artigosCurtidos', JSON.stringify(curtidos));
    },
    async enviarComentario() {
      if (!this.novoComentario.trim()) return
      try {
        const resposta = await fetch(`/api/artigos/${this.id}/comentarios`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            autor: 'Visitante',
            texto: this.novoComentario
          })
        })
        console.log('opa foi');

        const data = await resposta.json()
        this.artigo.comentarios.push(data.comentario)
        this.novoComentario = ''
        console.log(this.artigo.comentarios);

      } catch (error) {
        console.error('Erro ao enviar coment√°rio:', error)
      }
    },
    exportarPDF() {
      const doc = new jsPDF()
      doc.setFontSize(18)
      doc.text(this.artigo.titulo, 10, 20)
      doc.setFontSize(12)
      let y = 30
      this.artigo.conteudo.forEach(p => {
        const linhas = doc.splitTextToSize(p, 180)
        doc.text(linhas, 10, y)
        y += linhas.length * 8
      })
      doc.save(`${this.artigo.titulo}.pdf`)
    },
    formatDate(iso) {
  if (!iso) return 'sem data';

  // divide 9/11/2025 em [9,11,2025] (DD/MM/YYYY)
  const [dia, mes, ano] = iso.split('/');

  const d = new Date(`${ano}-${mes}-${dia}T00:00:00`);
  if (isNaN(d)) return iso;

  return d.toLocaleDateString('pt-BR');
}

  }
}
</script>

<style scoped>
.paragrafo {
  margin-bottom: 1rem;
  line-height: 1.6;
  font-size: 1.05rem;
}

.comentario {
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 6px;
  padding: 0.5rem;
}
</style>
